/* 搜索下拉框 */
#hotwords {
	background: #fff;
	@include shadow();
	
	position: absolute;
	top: 98px;
	left: 0.37rem;
	margin: 0 auto;
	width: 9.3rem;
	overflow: auto;
	overflow-x: hidden;
	/*  border: #D9D9D9 1px solid; */
	border-top: 0;
	
	li {
		line-height: 2.5em;
		text-indent: 2em;
		
		&:hover {
			color: #92d7ff;
		}
	}
}


